.topbar {
    width: 100%;
    position: fixed;
    top: 0;
    height: 60px;
    z-index: 101;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.05);
  
    @include themify($themes) {
      background: themed('colorBackground');
    }
  }
  
  .topbar_wrapper {
    position: relative;
    display: flex;
    height: 60px;
  }
  
  .topbar_button {
    width: 60px;
    height: 60px;
    display: flex;
    background: transparent;
    border: none;
    cursor: pointer;
    transition: 0.3s;
  
    &:focus {
      outline: none;
    }
  
    &:hover {
  
      @include themify($themes) {
        background: themed('colorHover');
      }
    }
  
    &.topbar_button--desktop {
      display: none;
    }
  }
  
  .topbar_button-icon {
    margin: auto;
    transition: all 0.3s;
    width: 16px;
  }
  
  .topbar_logo {
    width: 150px;
    height: 32px;
    margin: auto 0;
    background-repeat: no-repeat;
    background-position-y: center;
    background-position-x: left;
    background-size: contain;
    display: none;
  
    @include themify($themes) {
      margin-left: 15px;
      background-image: themed('logoImg');
    }
  
    @media screen and (min-width: 768px) {
      display: block;
    }
  }
  
  .topbar_right {
    position: absolute;
    right: 0;
    display: flex;
    height: 100%;
    margin-right: 15px;
  }
  
  .topbar_left {
    position: absolute;
    left: 0;
    display: flex;
    height: 100%;
    width: 50%;
  }
  
  .topbar_avatar {
    height: 100%;
    display: flex;
    cursor: pointer;
    position: relative;
    border-radius: 0;
    border: none;
    transition: all 0.3s;
    box-shadow: none;
    padding: 0;
    background-color: transparent;
  
    &:hover, &:focus, &:active, &:focus:active {
      @include themify($themes) {
        background-color: themed('colorHover');
      }
    }
  
    &:focus {
      outline: none;
    }
  
    &:before {
      display: none;
    }
  }
  
  .topbar_avatar-img, .topbar_avatar-name, .topbar_icon {
    margin: auto 0;
  }
  
  .topbar_avatar-img {
    border-radius: 50%;
    height: 36px;
    width: 36px;
  }
  
  .topbar_avatar-name {
    font-size: 13px;
    line-height: 18px;
    font-weight: 400;
    display: none;
    margin-left: 10px;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  }
  
  .topbar_icon {
    margin-left: 8px;
    height: 18px;
    margin-top: auto;
    fill: #b1c3c8;
  }
  
  .topbar_menu {
    width: 200px;
    border-radius: 0;
    border: none;
    padding: 15px 0;
    box-shadow: 0 2px 15px 0 rgba(0, 0, 0, 0.05);
    margin-top: 0;
  
    @include themify($themes) {
      background: themed('colorBackground');
    }
  
    button {
      padding: 0;
  
      &:hover {
        @include themify($themes) {
          background-color: themed('colorHover');
        }
      }
    }
  
    *:focus {
      outline: none;
    }
  }
  
  .topbar_menu-wrap {
    z-index: 101;
    position: absolute;
    width: 100%;
    padding: 0;
    min-width: 160px;
    right: 0;
  }
  
  .topbar_link {
    display: flex;
    padding: 9px 20px;
    transition: all 0.3s;
    height: 32px;
    width: 100%;
    position: relative;
    cursor: pointer;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  
    &:before {
      content: "";
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 2px;
      background: $color-accent;
      opacity: 0;
      transition: all 0.3s;
    }
  
    &:hover {
      @include themify($themes) {
        background-color: themed('colorHover');
      }
  
      &:before {
        opacity: 1;
      }
    }
  }
  
  .topbar_link-title {
    margin: 0;
    font-size: 14px;
    line-height: 16px;
  }
  
  .topbar_link-icon {
    margin-right: 15px;
    font-size: 13px;
    line-height: 13px;
  
    @include themify($themes) {
      color: themed('colorIcon');
    }
  }
  
  .topbar_menu-divider {
    margin: 15px 0;
  
    @include themify($themes) {
      border-top: 1px solid themed('colorBorder');
    }
  }
  
  .topbar_profile {
    margin-bottom: 0;
    margin-left: 20px;
    position: relative;
  }
  
  .topbar_collapse {
    position: relative;
    display: none;
  
    &.topbar_collapse--language {
      min-width: 70px;
      display: block;
  
      & > button {
        padding: 0 4px;
        width: 100%;
      }
    }
  
    @media screen and (min-width: 568px) {
      display: block;
    }
  }
  
  .topbar_collapse-content {
    width: 270px;
    position: absolute;
    right: 0;
    bottom: 20px;
    transform: translateY(100%);
    box-shadow: 0 10px 25px 0 rgba(33, 36, 50, 0.13);
    z-index: 101;
  
    @include themify($themes) {
      background: themed('colorBackground');
    }
  
    &.topbar_collapse-content--language {
      max-width: 70px;
      padding: 10px 0;
      width: 100%;
    }
  
    @media screen and (max-width: 768px) {
      left: 50%;
      transform: translate(-50%, 100%);
    }
  
    @media screen and (min-width: 520px) {
      width: 330px;
    }
  }
  
  .topbar_language-btn {
    padding: 4px 15px;
    width: 100%;
    border: none;
    background: transparent;
    cursor: pointer;
    text-align: left;
    font-size: 13px;
    line-height: 16px;
    transition: 0.3s;
  
    &:hover {
      color: $color-accent;
    }
  }
  
  .topbar_language-btn-title {
    display: flex;
    font-size: 11px;
    align-items: center;
    margin: auto 0;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  
    &:not(:last-child) {
      margin-right: 5px;
    }
  
    img {
      height: 11px;
      width: 16px;
      margin-right: 4px;
    }
  }
  
  .topbar_back {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: transparent;
    border: none;
  }
  
  .topbar_collapse-title-wrap {
    padding: 20px 15px 15px 15px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  
    @include themify($themes) {
      border-bottom: 1px solid themed('colorBorder');
    }
  }
  
  .topbar_collapse-item {
    padding: 12px 55px 12px 70px;
    display: flex;
    position: relative;
    height: 62px;
    align-items: center;
    flex-wrap: wrap;
  
    @include themify($themes) {
      border-bottom: 1px solid themed('colorBorder');
    }
  }
  
  .topbar_collapse-img-wrap {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    overflow: hidden;
    position: absolute;
    left: 15px;
  }
  
  .topbar_collapse-message {
    margin: 0;
    font-size: 12px;
    line-height: 16px;
    color: $color-gray;
  
    &.topbar_collapse-message--mail {
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
    }
  }
  
  .topbar_collapse-name {
    margin: 0;
    font-size: 12px;
    line-height: 16px;
    color: $color-accent;
  }
  
  .topbar_collapse-date {
    position: absolute;
    top: 12px;
    right: 15px;
    font-size: 10px;
    color: $color-gray;
    margin-top: 2px;
  }
  
  .topbar_collapse-link {
    display: block;
    padding: 10px;
    text-transform: uppercase;
    color: $color-accent;
    transition: 0.3s;
    text-align: center;
    font-weight: 500;
    font-size: 10px;
    line-height: 16px;
  
    &:hover {
      color: $color-accent-hover;
    }
  }
  
  .topbar_collapse-title {
    font-size: 14px;
    line-height: 16px;
  }
  
  .topbar_collapse-button {
    color: #c5d2d6;
    border: none;
    padding: 0;
    text-align: right;
    font-size: 12px;
    line-height: 16px;
    transition: 0.3s;
    background: transparent;
  
    &:hover {
      color: $color-accent;
    }
  }
  
  .topbar_btn {
    font-size: 18px;
    height: 100%;
    padding: 0 10px;
    cursor: pointer;
    position: relative;
    display: flex;
    border: none;
    background: transparent;
    transition: all 0.3s;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  
    &:hover {
  
      @include themify($themes) {
        background-color: themed('colorHover');
      }
    }
  
    svg {
      margin: auto;
      height: 18px;
      width: 18px;
      fill: #b1c3c8;
    }
  
    &.topbar_btn--new {
  
      .topbar_btn-new-label {
        position: absolute;
        right: 9px;
        top: 20px;
  
        & > div {
          position: relative;
  
          &:before {
            background-color: rgba(224, 83, 111, 0.2);
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            transform: translate(-50%, -50%);
            animation: beforePulse 1.5s infinite;
          }
  
          &:after {
            height: 7px;
            width: 7px;
            background-color: #e0536f;
            content: "";
            position: absolute;
            top: 50%;
            left: 50%;
            border-radius: 50%;
            transform: translate(-50%, -50%);
          }
        }
      }
    }
  
    @keyframes beforePulse {
      from {
        width: 7px;
        height: 7px;
      }
      25% {
        width: 13px;
        height: 13px;
      }
      to {
        width: 7px;
        height: 7px;
      }
    }
  }
  
  .topbar_nav {
    width: 100%;
    display: none;
    height: 100%;
    justify-content: center;
    align-items: center;
  }
  
  .topbar .topbar_nav-dropdown-toggle {
    height: 60px;
    background: transparent;
    border-radius: 0;
    border: none;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  
    &:before {
      display: none;
    }
  
    &:hover, &:focus, &:active, &:focus:active {
  
      @include themify($themes) {
        background-color: themed('colorHover');
      }
    }
  
    svg {
      fill: $color-additional;
      margin-left: 3px;
      height: 16px;
      width: 16px;
      margin-right: 0;
    }
  }
  
  .topbar_nav-dropdown-menu {
    width: 240px;
    border-top: 2px solid $color-accent;
  
    button {
      padding: 0;
    }
  }
  
  .topbar_link-badge {
    width: 26px;
    height: 14px;
    background-color: $color-red;
    font-size: 8px;
    font-weight: 400;
    padding: 2px;
    margin-left: 5px;
    line-height: 9px;
    position: relative;
    text-transform: uppercase;
    border-radius: 7px;
  
    span {
      position: absolute;
      left: 0;
      top: 3px;
      width: 26px;
      text-align: center;
    }
  }
  
  .topbar_nav-link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 60px;
    padding: 10px 25px;
    transition: 0.3s;
    font-size: 14px;
  
    @include themify($themes) {
      color: themed('colorText');
    }
  
    &:hover {
  
      @include themify($themes) {
        background-color: themed('colorHover');
      }
    }
  }
  
  .topbar_category-wrap {
    position: relative;
  
    &:hover {
  
      .topbar_submenu {
        opacity: 1;
        width: auto;
        height: auto;
      }
    }
  }
  
  .topbar_category-icon {
    position: absolute;
    right: 20px;
    font-size: 10px;
    line-height: 14px;
  
    @include themify($themes) {
      color: themed('colorIcon');
    }
  }
  
  .topbar_submenu {
    position: absolute;
    right: 1px;
    top: 0;
    transform: translateX(100%);
    transition: 0.3s;
    opacity: 0;
    width: 0;
    height: 0;
    overflow: hidden;
  
    @include themify($themes) {
      background-color: themed('colorHover');
    }
  
    .topbar_link {
  
      &:hover {
  
        @include themify($themes) {
          background-color: themed('colorBackground');
        }
      }
    }
  }
  
  .topbar_search {
    display: none;
    margin: auto 0;
    padding: 0;
    position: relative;
  }
  
  .topbar_search-field {
    width: 0;
    transition: all 0.3s;
    opacity: 0;
    margin: auto auto auto 0;
    border: none;
    border-radius: 13px;
    height: 26px;
    padding-left: 10px;
    padding-right: 46px;
  
    @include themify($themes) {
      background-color: themed('colorBackgroundBody');
    }
  
    &.topbar_search-field--open {
      width: 200px;
      opacity: 1;
      margin-left: 10px;
  
      & + button {
        right: 10px;
      }
    }
  
    &:focus {
      outline: none;
    }
  }
  
  .topbar_search-btn {
    height: 26px;
    width: 26px;
    border-radius: 13px;
    border: none;
    padding: 0;
    background: transparent;
    position: absolute;
    right: 0;
  
    &:hover {
  
      @include themify($themes) {
        background-color: themed('colorBackgroundBody');
      }
    }
  }
  
  @media screen and (min-width: 480px) {
  
    .topbar_avatar-name {
      display: block;
    }
  
    .topbar_menu {
      width: 100%;
      left: 0 !important;
    }
  }
  
  @media screen and (min-width: 576px) {
  
    .topbar_button {
  
      &.topbar_button--desktop {
        display: block;
      }
  
      &.topbar_button--mobile {
        display: none;
      }
    }
  
    .topbar.topbar--navigation {
  
      .topbar_button.topbar_button--mobile {
        display: block;
      }
    }
  }
  
  @media screen and (min-width: 768px) {
  
    .topbar_search {
      display: flex;
    }
  }
  
  @media screen and (min-width: 992px) {
  
    .topbar_nav {
      display: flex;
    }
  
    .topbar.topbar--navigation {
  
      .topbar_logo {
        margin-left: 15px;
        display: block;
      }
  
      .topbar_button.topbar_button--mobile {
        display: none;
      }
  
      .topbar_avatar-name {
        display: none;
      }
  
      .topbar_profile {
        margin-left: 0;
      }
    }
  }
  
  @media screen and (min-width: 1200px) {
  
    .topbar.topbar--navigation {
  
      .topbar_avatar-name {
        display: block;
      }
    }
  }
  
  @media screen and (min-width: 1580px) {
  
    .topbar_nav-dropdown-toggle, .topbar_nav-link {
      width: 240px;
    }
  }